//fonts
$quicksand: 'Quicksand', sans-serif;

//colors
$orange: #ffa500;
$off-white: #f8f8f8;
$green: #57b846;
$success-text: #4caf50;
$red: #ea4355;
$error-text: #f44336;
$blue: #1da1f2;
$white: #fff;
$off-black: #333;

// sizes
$xs: 0.5rem;
$normal: 1rem;
$sm: 1.5rem;
$md: 2rem;
$lg: 3rem;
$xlg: 5rem;
$xxlg: 7rem;

// button radius
$button-radius: 30px;

//breakpoints

$lg-breakpoint: 1024px;
$md-breakpoint: 768px;
$sm-breakpoint: 641px;
$xs-breakpoint: 400px;

//shadow
$shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12),
  0 1px 5px 0 rgba(0, 0, 0, 0.2);
