#home {
  background-image: url(../../assets/img/background.jpg);
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: space-around;
  height: 100vh;
  width: 100vw;
  section {
    background-color: rgba(0, 0, 0, 0.7);
    border-radius: 7px;
    padding-left: $md;
    padding-right: $md;
    height: 80%;
    width: 35%;
    margin-top: 1rem;
  }

  .cube {
    font-size: 8rem;
    color: $orange;
  }

  h1 {
    font-size: $lg;
    font-weight: 500;
    text-align: center;
    color: $off-white;
  }

  .play-button-container {
    width: 100%;
  }

  a.play-button {
    background-color: $green;
    display: block;
    border-radius: $button-radius;
    color: #fff;
    cursor: pointer;
    padding: $normal;
    text-align: center;
    transition: 0.1s linear all;
  }

  a.play-button:hover {
    background-color: darken($green, 20%);
    box-shadow: $shadow;
  }
  .auth-container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-top: $lg;
  }

  #login-button {
    background-color: $red;
  }

  #login-button:hover {
    background-color: darken($red, 20%);
    box-shadow: $shadow;
  }
  #signup-button {
    background-color: $blue;
  }

  #signup-button:hover {
    background-color: darken($blue, 20%);
    box-shadow: $shadow;
  }
  .auth-container .auth-buttons {
    color: #fff;
    cursor: pointer;
    border-radius: $button-radius;
    padding: $normal;
    text-align: center;

    transition: 0.1s linear all;
    width: 45%;
  }
}

@media (max-width: 768px) {
  #home section {
    padding: 0 $normal;
    width: 100%;
    height: 100%;
  }
}

.logo {
  padding-top: 30px;
  padding-bottom: 0;
}

.website {
  padding: 10px;
  margin-top: 40px;
  font-size: 14px;
  display: flex;

  justify-content: center;
  cursor: pointer;
  transition: 0.3s linear all;
}

.website:hover {
  color: #fff !important;
  font-size: 15px;
  font-weight: bold;
}
