@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
*{padding:0;margin:0}body{font-family:"Quicksand",sans-serif}.toast-invalid{background-color:#f44336 !important;border-radius:5px !important;color:#f8f8f8 !important}.toast-valid{background-color:#4caf50 !important;border-radius:5px !important;color:#f8f8f8 !important}#home{background-image:url(/TI-testApp/static/media/background.656da130.jpg);background-size:cover;background-position:center;display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;height:100vh;width:100vw}#home section{background-color:rgba(0,0,0,0.7);border-radius:7px;padding-left:2rem;padding-right:2rem;height:80%;width:35%;margin-top:1rem}#home .cube{font-size:8rem;color:orange}#home h1{font-size:3rem;font-weight:500;text-align:center;color:#f8f8f8}#home .play-button-container{width:100%}#home a.play-button{background-color:#57b846;display:block;border-radius:30px;color:#fff;cursor:pointer;padding:1rem;text-align:center;transition:0.1s linear all}#home a.play-button:hover{background-color:#346e2a;box-shadow:0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.12),0 1px 5px 0 rgba(0,0,0,0.2)}#home .auth-container{width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;margin-top:3rem}#home #login-button{background-color:#ea4355}#home #login-button:hover{background-color:#b31425;box-shadow:0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.12),0 1px 5px 0 rgba(0,0,0,0.2)}#home #signup-button{background-color:#1da1f2}#home #signup-button:hover{background-color:#0967a0;box-shadow:0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.12),0 1px 5px 0 rgba(0,0,0,0.2)}#home .auth-container .auth-buttons{color:#fff;cursor:pointer;border-radius:30px;padding:1rem;text-align:center;transition:0.1s linear all;width:45%}@media (max-width: 768px){#home section{padding:0 1rem;width:100%;height:100%}}.logo{padding-top:30px;padding-bottom:0}.website{padding:10px;margin-top:40px;font-size:14px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;cursor:pointer;transition:0.3s linear all}.website:hover{color:#fff !important;font-size:15px;font-weight:bold}.instructions{border:1px solid #dfdfdf;border-radius:5px;margin-top:3rem;margin-bottom:3rem;padding:1.5rem 3rem}.instructions h1{color:#333;font-size:2rem;text-align:center}.instructions .lifeline{position:relative;top:-3px}.instructions .lifeline-icon{color:green;cursor:pointer}.instructions .browser-default{padding-left:30px;margin-top:10px;margin-bottom:15px}.instructions img{width:80%;height:50%}@media (max-width: 768px){.instructions img{width:100% !important;height:150% !important}}.questions{background-color:#f8f8f8;border-left:5px solid #1da1f2;width:80%;margin:3rem auto 0 auto;padding:1.5rem 2rem}.questions .branding{display:-webkit-inline-flex;display:inline-flex}.questions h2{font-weight:bold;text-align:center}.questions .innovators{color:#0967a0}.questions .lifeline-container{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.questions .lifeline{position:relative;top:-3px}.questions .lifeline-icon{color:#57b846;cursor:pointer}.questions .lifeline-icon:hover{font-weight:bolder;color:#cc8400;background:none}.questions h5{font-size:1.2rem;margin-bottom:1g;line-height:1.5;text-align:center}.questions .options-container{display:inline-block;width:50%}.questions .option{background-color:#1da1f2;border-radius:30px;color:#fff;cursor:pointer;margin:2rem auto;padding:1rem;transition:0.3s linear all;text-align:center;width:90%}.questions .option:hover{background-color:#085788}.bottom-container{display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;margin:0 auto 1.5rem auto;padding:2rem;width:80%}.button-container button{border:none;color:#f8f8f8;cursor:pointer;font-family:"Quicksand",sans-serif;margin-right:2rem;padding:.5rem 1.5rem}.button-container button:first-child{background-color:#0c85d0;box-shadow:0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.12),0 1px 5px 0 rgba(0,0,0,0.2);transition:0.2s linear all}.button-container button:first-child:hover{background-color:#0967a0}.button-container button:nth-child(2){background-color:#469338;box-shadow:0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.12),0 1px 5px 0 rgba(0,0,0,0.2);transition:0.2s linear all}.button-container button:nth-child(2):hover{background-color:#346e2a}.button-container button:last-child{background-color:#e1192f;box-shadow:0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.12),0 1px 5px 0 rgba(0,0,0,0.2);transition:0.2s linear all}.button-container button:last-child:hover{background-color:#b31425}

